import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Index from '@/pages/index'
import login from '@/pages/login'
import test from '@/pages/test'
import test2 from '@/pages/test2'
import playVideo from '@/pages/playVideo'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: login
    }, {
      path: '/index',
      name: 'Index',
      component: Index
    }, {
      path: '/login',
      name: 'login',
      component: login
    }, {
      path: '/playVideo',
      name: 'playVideo',
      component: playVideo
    }, {
      path: '/test',
      name: 'test',
      component: test
    }, {
      path: '/test2',
      name: 'test2',
      component: test2
    }
  ]
})



