<template>
    <div class="myVideo">
        <video id="videoPlayer" class="video-js vjs-default-skin" controls playsinline autoplay="autoplay" width="100%">
            <source :src="attachmentLink" type="application/x-mpegURL" />
        </video>
    </div>
</template>

<script>
import videojs from "video.js";
import "video.js/dist/video-js.css";
export default {
    data() {
        return {
            dp: null,
            options: {
                autoplay: false, // 设置自动播放
                muted: true, // 设置了它为true，才可实现自动播放,同时视频也被静音（Chrome66及以上版本，禁止音视频的自动播放）
                preload: "auto", // 预加载
                controls: true, // 显示播放的控件
                fluid: true,
            },
        };
    },
    mounted() {
        // 使用 $nextTick 解决vedio报错  The element or ID supplied is not valid. (videojs)
        this.$nextTick(() => {
            this.loadVideo();
        });
    },
    methods: {
        loadVideo() {
            this.db = videojs("videoPlayer", this.options);
            // 也可以使用以下方式给vedio设置 src
            this.db.src([
                {
                    src: "https://bitdash-a.akamaihd.net/content/sintel/hls/playlist.m3u8", // 地址
                    type: "application/x-mpegURL", // 告诉videojs,这是一个hls流
                },
            ]);
        },
        // 销毁
        beforeDestroy() {
            if (this.dp) {
                this.dp.dispose(); // dispose()会直接删除Dom元素
            }
        },
    },
};
</script>

<style scoped>
.myVideo{
    width: 100vw;
}
.video-box {
    width: 100%;
    max-width: 500px;
    max-height: 500px;
}

::v-deep .video-js .vjs-big-play-button {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
</style>




