<template>
    <div>
        <div style="display: flex;height: 100vh;">
            <div style="width: 20vw;height: 100%; border: 1px black solid;">
                <img src="../assets/image/304e5f95ba1655656db3096d7d30cf8.jpg"
                    style="width: 100px;height: 100px;margin-top: 30px;" />
                <div class="title">MTR iCCTV Demo</div>
            </div>
            <div style="width: 80vw;height: 100%; border: 1px black solid;">
                <div
                    style="width: 77vw;height: 70px; border: 1px black solid; line-height: 70px;display: flex;flex-direction: row-reverse;padding-right: 3vw;">
                    <span style="cursor: pointer;" @click="logout">Hi Admin | Logout</span>
                </div>
                <div style="display: flex;margin: 1vw;">
                    <video id="videoPlayer0" width="400" height="250" controls></video>
                    <!-- <video id="videoPlayer1" width="400" height="250" controls></video> -->
                        <video id="videoPlayer" class="video-js vjs-default-skin" controls playsinline width="500px">
                            <source :src="attachmentLink" type="application/x-mpegURL" />
                        </video>
                    <video id="videoPlayer2" width="400" height="250" controls></video>


                </div>
                <div style="display: flex;margin: 1vw;">
                    <div class="myVideo" style="width: 30vw;height: 250px;margin-right: 10px;" @click.stop="onplay(2)">
                        <video id="videoPlayer4" width="400" height="250" controls></video>

                        <!-- <video id="videoPlayer2" class="video-js vjs-default-skin" controls playsinline 
                            width="500px">
                            <source :src="attachmentLink" type="application/x-mpegURL" />
                        </video> -->
                    </div>
                    <div style="border: 1px black solid;width: 100%;height: 250px; text-align: left;">
                        <div style="margin: 10px 0 0 10px;">CCTY 1 Train Motion Detected</div>
                        <div style="border: 1px black solid;margin: 10px;height: 200px;">
                            <div>Tmestamp 1</div>
                            <div>Tmestamp 2</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import videojs from "video.js";
import "video.js/dist/video-js.css";
import flvjs from "flv.js"
export default {
    data() {
        return {
            dp: null,
            options: {
                autoplay: false, // 设置自动播放
                muted: false, // 设置了它为true，才可实现自动播放,同时视频也被静音（Chrome66及以上版本，禁止音视频的自动播放）
                preload: "auto", // 预加载
                controls: false, // 显示播放的控件
                language: 'zh-CN',
                width: '400px',
                height:"250"
                // fluid: true,
            },
            attachmentLink: ""
        };
    },
    mounted() {
        // 使用 $nextTick 解决vedio报错  The element or ID supplied is not valid. (videojs)
        this.$nextTick(() => {
            console.log('9887');
            this.loadVideo();
        });
    },
    methods: {
        onplay(type) {

        },
        loadVideo() {
            var videoElement = document.getElementById('videoPlayer0');
            var flvPlayer = flvjs.createPlayer({
                type: 'flv',
                url: 'http://66.203.157.36:39000/flv?app=rtsp&stream=mydemo'
            });
            flvPlayer.attachMediaElement(videoElement);
            flvPlayer.load();
            flvPlayer.play();

            this.db2 = videojs("videoPlayer", this.options);
            this.db2.src([
                {
                    src: "http://66.203.157.36:39000/hls01/mydemo1.m3u8", // 地址
                    type: "application/x-mpegURL", // 告诉videojs,这是一个hls流
                },
            ]);

            var videoElement2 = document.getElementById('videoPlayer2');
            var flvPlayer2 = flvjs.createPlayer({
                type: 'flv',
                url: 'http://66.203.157.36:39000/flv?app=flv03&stream=mydemo3'
            });
            flvPlayer2.attachMediaElement(videoElement2);
            flvPlayer2.load();
            flvPlayer2.play();

            var videoElement4 = document.getElementById('videoPlayer4');
            var flvPlayer4 = flvjs.createPlayer({
                type: 'flv',
                url: 'http://66.203.157.36:39000/flv?app=flv02&stream=mydemo2'
            });
            flvPlayer4.attachMediaElement(videoElement4);
            flvPlayer4.load();
            flvPlayer4.play();


        },
        logout() {
            this.beforeDestroy()
            this.$router.push({ path: "/" })
        },
        // 销毁
        beforeDestroy() {
            if (this.db) {
                this.db.dispose(); // dispose()会直接删除Dom元素
                this.db2.dispose(); // dispose()会直接删除Dom元素
            }
        },
    },
};
</script>

<style scoped>
.title {
    margin-top: 20px;
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 20px;
}

.video-box {
    width: 100%;
    max-width: 200px;
    max-height: 200px;
}

::v-deep .vjs-big-play-button {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

::v-deep.vjs-big-play-button {
    display: none !important;
}

/deep/ .vjs-default-skin .vjs-big-play-button {
    display: none !important;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

::v-deep .video-js {
    width: 20vw;
}
</style>




