<template>
  <div>

    <div>
        <img src="../assets/image/304e5f95ba1655656db3096d7d30cf8.jpg" style="width: 150px;height: 150px;margin-top: 30px;"/>
        <div class="title">MTR iCCTV Demo</div>
        <div style="display: flex;flex-direction: column;width: 220px;margin: 0 auto;text-align: left;">
            <span>User ID</span>
            <input v-model="loginForm.username" style=""/>
            <span>Password</span>
            <input v-model="loginForm.password" type="password"/>
        </div>
        <button @click="login">Login</button>
        <div>by Big Blocks Limited / DBC</div>

    </div>

  </div>
</template>

<script>
export default {
    data() {
        return {
            loginForm: {
                username: "Admin",
                password: "123456",
            },
        }
    },
    methods: {
        login(){
            this.$router.push({ path: '/index'})
        }
    }

}
</script>

<style scoped>

.title{
    margin-top:20px ;
    font-size: 26px;
    font-weight: 700;
    margin-bottom: 20px;
}
input{
    height: 20px;
    margin-top: 5px;
}
span{
    margin-top: 10px;
    font-weight: 900;
}
button{
    width: 220px;
    margin: 20px 0 20px 0;
    height: 40px;
    background-color: white;
    font-size: 18px;
    font-weight: 700;
}

</style>


